import React from 'react';

class Stream extends React.Component {
  	constructor(props) {
    	super(props);
    	this.state = {userList: []};
    }

    addWhiteSpace(number) {
        if (number < 10) {
            return "0" + number.toString();
        }
        else {
            return number.toString();
        }
    }

    dateTimeString(timeStamp) {
        var today = new Date(timeStamp*1000);
        var date = today.getFullYear() + "-" + this.addWhiteSpace(today.getMonth()+1) + "-" + this.addWhiteSpace(this.addWhiteSpace(today.getDate()));
        var time = "(" + this.addWhiteSpace(today.getHours()) + ":" + this.addWhiteSpace(today.getMinutes()) + ":" + this.addWhiteSpace(today.getSeconds()) + ")";
        var dateTime = date + " " + time;
        return dateTime;
    }

    startStream() {
	    var stream = new EventSource("http://chat.cs291.com/stream/" + this.props.token);
        // var stream = new EventSource("http://localhost:4000/stream/" + this.props.token);
	    
	    stream.addEventListener(
	    	"Disconnect", 
	    	(event) => {
                console.log(this.props.token);
	    		stream.close();
	            this.props.updateToken("");
	        	},
	        false
	    );

	    stream.addEventListener(
        	"ServerStatus",
        	(event) => {
            	this.props.updateMessageList(
                        [this.dateTimeString(JSON.parse(event.data).created) + " " + 
                        JSON.parse(event.data).status]);
        	},
        	false
    	);

    	stream.addEventListener(
    		"Join",
    		(event) => {
                this.state.userList.push(JSON.parse(event.data).user);
                this.props.updateUserList(this.state.userList);
                this.props.updateMessageList(
                        [this.dateTimeString(JSON.parse(event.data).created) + " " + 
                        "JOINED: " + JSON.parse(event.data).user]);
        	},
        	false
    	);

    	stream.addEventListener(
        	"Part",
        	(event) => {
                let userToRemove = JSON.parse(event.data).user;
            	this.state.userList = this.state.userList.filter(function(user) {return user !== userToRemove});
                this.props.updateUserList(this.state.userList);
                this.props.updateMessageList(
                        [this.dateTimeString(JSON.parse(event.data).created) + " " + 
                        "PARTED: " + JSON.parse(event.data).user]);
        	},
        	false
    	);

    	stream.addEventListener(
        	"Message",
        	(event) => {
                this.props.updateMessageList(
                        [this.dateTimeString(JSON.parse(event.data).created) + " " + 
                         JSON.parse(event.data).user + " " +
                         ": " + JSON.parse(event.data).message]);
                this.props.updateNumMessages();
            },
        	false
    	);

    	stream.addEventListener(
        	"Users",
        	(event) => {
                this.state.userList = JSON.parse(event.data).users;
                this.props.updateUserList(this.state.userList);
        	},
        	false
    	);

    } // End of startStream function

}
export default Stream;