import React from 'react';
import Stream from './Stream.jsx';

class LoginRequest extends React.Component {
  	constructor(props) {
    	super(props);
    	this.state = {};
    }

    sendRequest() {
    	var request = new XMLHttpRequest();
	    var form = new FormData();
	    form.append("password", this.props.password);
	    form.append("username", this.props.username);
	    request.open("POST", "http://chat.cs291.com/login");
	    // request.open("POST", "http://localhost:4000/login");
	    request.onreadystatechange = () => {
	     	console.log(request);
	    	if (request.readyState !== 4) return;
	    	if (request.status === 201){
	    		// Extract the token
	    		var accessToken = JSON.parse(request.responseText).token;
	    		this.props.updateToken(accessToken);
	    		var stream = new Stream({"token": accessToken, updateToken: this.props.updateToken, updateMessageList: this.props.updateMessageList, updateUserList: this.props.updateUserList, updateNumMessages: this.props.updateNumMessages});
	    		stream.startStream();
	    	}
	    	else if (request.status === 403){
	    		alert("Invalid username or password");
	    	}
	    	else {
	    		alert(request.status + " failure to /login");
	    	}
	    };
	    request.send(form);
	  };
}
export default LoginRequest;