import React from 'react';
import './App.css';
import Login from './Login';
import Compose from './Compose';
import MessageList from './MessageList.jsx';
import UserList from './UserList.jsx';
import Stream from './Stream.jsx';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {token: "", messageList: [], userList: [], numMessages: 0};
    }

  updateToken = (token) => {
  	this.setState((prevState, props) => ({
      token: token
    }));
  };

  updateMessageList = (message) => {
  	this.setState((prevState, props) => ({
      messageList: [...prevState.messageList, message]
    }));
  };

  updateUserList = (userList) => {
  	this.setState((prevState, props) => ({
      userList: userList
    }));
  };

  updateNumMessages = () => {   	
  	this.setState((prevState, props) => ({
      numMessages: this.state.numMessages+1
    }));
  };

  componentDidMount(){
    document.title = "Homemade Chat Client"
  }

  render(){
  	let node;
  	if (this.state.token !== ""){
  		var newUserSet = new Set(this.state.userList);
  		var newUserList = [...newUserSet];
  		node = (
  			<>
  			<br></br>
  			<h1>Welcome to our Homemade Chat Client!</h1>
  			<br></br>
  			<div class="NumMessages">
  			Number of Messages: {this.state.numMessages}</div>
  			<br></br><br></br>
  			<div class="TopDisplay">
  			<MessageList messageList={this.state.messageList}/>
  			<UserList userList={newUserList}/>
  			</div>
  			<br></br>
  			<Compose token={this.state.token}/>
  			</>
  			)
  		// Pass in the token as props
  	}
  	else
  		node = (<><div class="Centered">
  			   <Login updateToken={this.updateToken} 
  			    updateMessageList={this.updateMessageList} 
  			    updateUserList={this.updateUserList}
  			    updateNumMessages={this.updateNumMessages}/></div>
  			   </>)
  		// Pass in the updateToken function as props
  return(node);
  }
}

export default App;
