import React from 'react';
import LoginRequest from './LoginRequest.jsx';
import './Login.css'

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: ""
      };
    }

    onSubmit = event => {
    	event.preventDefault();
    	var loginRequest = new LoginRequest({"username": this.state.username, "password": this.state.password, "updateToken": this.props.updateToken, "updateMessageList": this.props.updateMessageList, "updateUserList": this.props.updateUserList, "updateNumMessages": this.props.updateNumMessages});
    	loginRequest.sendRequest();
    };

    onChange = event => {
		this.setState({ [event.target.name]: event.target.value });
    };

    render() {
		return (
		    <>
		    <h1>Halt: Who Goes There?</h1>
		    <br></br><br></br><br></br><br></br>
		    <form onSubmit={this.onSubmit}>
		    <label> Username:
		    <br></br>
		    <input name="username" type="text" value={this.state.username} onChange={this.onChange}/>
		    </label>
		    <br></br>
		    <label> Password:
		    <br></br>
		    <input name="password" type="password" value={this.state.password} onChange={this.onChange}/>
		    </label>
		    <br></br><br></br>
	        <input type="submit" value="Login" />
	        </form> 
	        </> );
	    };
	};
export default Login;