import React from 'react';
import PostRequest from './PostRequest.jsx';
import './Compose.css'

class Compose extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      message: "",
      };
    }

    onSubmit = event => {
	    event.preventDefault();
	    var postRequest = new PostRequest({"message": this.state.message, "token": this.props.token});
	    postRequest.sendRequest();
    	this.setState({message: ""});
     };

    onChange = event => {
    	this.setState({ [event.target.name]: event.target.value });
  	};

    render() {
	return (
	    <>
	    <form onSubmit={this.onSubmit}>
	    <input name="message" type="text" value={this.state.message} onChange={this.onChange}/>
    	</form> 
    	</>
    	);
	}

}
export default Compose;